<template>
  <section class="bill-registration">
    <!-- page title, description -->
    <div class="text-center mb-2">
      <div class="d-flex align-items-center justify-content-center mb-2">
        <b-avatar class="mr-1" size="40" variant="light-danger">
          <img :src="require('@/assets/icons/building_icon.svg')" alt="" draggable="false" width="30" />
        </b-avatar>
        <h4 class="m-0">تعریف قبض / فاکتور</h4>
      </div>
      <span>
        در این بخش می‌توانید قبض یا فاکتور جدید را تعریف کنید. با انتخاب مشمول می‌توانید مشخص کنید که قبض بین مالکین یا ساکنین تقسیم شود.
      </span>
    </div>

    <!-- content list -->
    <div ref="expensesList">
      <b-card class="card-one">
        <div class="d-flex justify-content-start align-items-center">
          <!-- icon -->
          <b-avatar class="mr-1" size="36" style="background: #fceaeb">
            <chart-icon :style="{ color: 'var(--danger)' }" />
          </b-avatar>

          <!-- title -->
          <h4 class="title">عنوان های موجود</h4>
        </div>

        <!-- loading -->
        <div v-if="loading" class="d-flex flex-column justify-content-center align-items-center">
          <b-spinner style="margin-top: 10px; width: 3rem; height: 3rem" variant="primary" />
        </div>

        <!-- content -->
        <div class="debt-categories-items" v-if="!loading">
          <ul class="p-0 m-0">
            <li v-for="(item, index) in debtCategories" :key="index" class="debt-categories-items-list cursor-pointer">
              <div class="d-flex align-items-center justify-content-between" @click="selectItem(item)">
                <div class="d-flex align-items-center">
                  <p class="m-0">{{ item.debt_category_name }}</p>
                </div>
                <!-- edit-icon -->
                <div class="d-flex justify-content-end cursor-pointer" @click="debtCategory = item.debt_category_name">
                  <edit-icon />
                </div>
              </div>
            </li>
          </ul>
        </div>
      </b-card>
    </div>

    <!-- add new section -->
    <b-card class="card-two">
      <!-- icon & title -->
      <div class="d-flex justify-content-start align-items-center">
        <!-- icon -->
        <b-avatar class="mr-1" size="36" variant="light-info">
          <chart-icon :style="{ color: 'var(--info)' }" />
        </b-avatar>

        <!-- title -->
        <h4 class="title">تعریف عنوان جدید</h4>
      </div>

      <div class="mt-2">
        <!-- input form -->
        <div>
          <label for="debt-category-name" class="form-label first-one f16">عنوان قبض / فاکتور </label>
          <input ref="debt_category_name" type="title" class="form-control p-2" id="debt-category-name" v-model="debt.debt_category_name" />
        </div>

        <div class="included-bill">
          <p class="f16">مشمول قبض</p>
          <b-form-group>
            <b-form-radio-group id="" v-model="debt.debtor_id" name="" class="d-flex justify-content-between form-input" :options="optionsradio" />
          </b-form-group>
        </div>

        <!-- action btn -->
        <div class="action-btn">
          <b-button block variant="primary" @click="store"> ثبت </b-button>
        </div>
      </div>
    </b-card>
  </section>
</template>

<script>
import {
  BAvatar,
  BCard,
  BRow,
  BFormSelect,
  BButton,
  BFormRadio,
  BFormGroup,
  BFormRadioGroup,
  BSpinner
} from "bootstrap-vue";
import ChartIcon from "@core/components/svg-icons/ChartIcon.vue";
import vSelect from "vue-select";
import EditIcon from "@/@core/components/svg-icons/EditIcon.vue";
import ToastificationContent from "@core/components/toastification/ToastificationContent.vue"
import { getDebtCategory, storeDebtCategory } from "/src/api/costsServices";

export default {
  data() {
    return {
      debtor: true,
      debtCategories: [],
      debtCategory: null,
      debt: {
        debt_category_id: null,
        debt_category_name: null,
        debtor_id: "2",
        debtor_type: 5
      },
      loading: false,
      optionsradio: [
        { text: "ساکن", value: "1" },
        { text: "مالک", value: "2" },
      ],
    };
  },
  components: {
    ChartIcon,
    BAvatar,
    BRow,
    BCard,
    BFormSelect,
    BButton,
    vSelect,
    BFormRadio,
    BFormGroup,
    BFormRadioGroup,
    EditIcon,
    BSpinner
  },
  async mounted() {
    this.getDebtCategories()
  },
  methods: {
    /**get refresh debtCategories */
    async getDebtCategories() {
      try {
        this.loading = true
        const { data } = await getDebtCategory({ debt_type: [5] });
        this.debtCategories = data.debtor_list.list;
      } catch (error) {
        console.error(error);
      }
      this.loading = false
    },

    // store/update debtCategory
    async store() {
      try {
        if (!this.debt.debt_category_name) return this.$toast({
          component: ToastificationContent,
          props: {
            title: "عنوان قبض الزامی است",
            icon: 'AlertCircleIcon',
            variant: 'danger',
          },
        })

        const { data } = await storeDebtCategory(this.debt)
        this.$toast({
          component: ToastificationContent,
          props: {
            title: data.message,
            icon: 'XCircle',
            variant: 'success',
          },
        })
        // redirect back if exists
        if (this.$route.query.back) {
    			this.$router.push({ name: this.$route.query.back })
          return
        }
        this.getDebtCategories()

        this.debt = {
          debt_category_id: null,
          debt_category_name: null,
          debtor_id: "2",
          debtor_type: 5
        }

        /**scroll to list */
        window.scrollTo(0, 0);
      } catch (error) {
        console.error(error)
      }
    },

    selectItem(item) {
      this.debt = item

      /**make title input focus */
      this.$refs.debt_category_name.focus()
    }
  }
};
</script>

